form{
    width: 70%;
    margin: auto;
    padding: 0;
    max-height: 50%;
}
.bgCouleur{
    color: white;
    background-color: #f03a68;
}
.inpuut{
    padding: 10px;
    width: 40%;
    border: solid 1px lightslategray;
    border-radius: 5px;
    font-size: 1em;
}
.add{
    
    
    background-color:#22192c ;
    color: white;
    
}
#delete{
    color: white;
    background-color: #f03a68;
    border-radius: 2px;
    border-color: transparent;
    text-align: center;
    width: 4vw;
    margin-right: 5px;
}
ul{
    width: 70%;
    margin: auto;  
    padding: 0;
}
ul li{
    list-style: none;
    padding: 5px;
    border: solid 1px lightslategray;
    border-radius: 5px; 
    text-align: center;
    background-color: white;
    color: #22192c;
    margin-bottom: 10px;
}
h1,h2{
    text-align: center;
    color: white;
    font-size: 2em;
}
h2{
    color: #f03a68;
}
.flex{
    display: flex;
    justify-content: space-between;
}
